.MenuItem {
    display: flex;
    flex-direction: column;
    background: var(--primary-background-light);
    width: 365px;
    height: 524px;
    margin: 50px;
    padding: 50px;
    border-radius: 32px;
    text-align: center;
    align-items: center;
    cursor: pointer;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all .3s;

    &:hover {
        background: var(--primary-color);
    }

    &__Icon {
        width: 122px;
        height: 122px;
        margin-bottom: 40px;
    }

    &__Name {
        font-weight: 700;
        font-size: 34px;
        line-height: 40px;
        margin-bottom: 20px;
    }

    &__Description {
        font-size: 24px;
        line-height: 40px;
    }
}
