@import "./../../style/base/Card.scss";

.DatabaseCard {
    @extend %Card;
    min-height: 100vh;
    height: unset;

    &__Items {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }

    &__Item {
        background: var(--primary-background-light);
        border-radius: 25px 25px;
        margin-bottom: 50px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        margin: 15px;
        height: max-content;
    }

    &__ItemContent {
        padding: 5px 25px 15px 25px;
    }

    &__Image {
        max-height: 462px;
        width: 100%;
        border-radius: 25px 25px 0 0;
        object-fit: cover;
    }

    &__Title {
        width: 100%;
        text-align: center;
        font-weight: bold;
        display: flex;
        place-content: center;
        padding-bottom: 5px;
    }

    &__Params {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__Param {
        display: flex;
        width: 100%;
        justify-content: space-between;
    }

    &__Header {
        text-align: center;
        color: var(--primary-color);
        font-size: 40px;
    }
}
