@import "./../../style/base/Card.scss";

.CalculatorCard {
    @extend %Card;
    width: 100%;
    height: unset;
    min-height: 100vh;

    &__Calculator {
        display: flex;
        flex-direction: row;
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    &__Inputs {
        display: flex;
        flex-direction: column;
        width: 60%;
        margin: 50px;
    }

    &__Title {
        margin-bottom: 10px;
        color: var(--primary-color);
    }

    &__Input {
        display: flex;
        flex-direction: column;
        margin-bottom: 35px;
    }

    &__InputRow {
        display: flex;

        .MuiTextField-root {
            margin-right: 15px !important;
        }
    }

    &__Result {
        width: 55%;
        margin: 50px;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-height: 100vh;
    }

    &__ResultCard {
        background: var( --primary-background-light);
        border-radius: 25px;
        margin-bottom: 50px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        width: 100%;

        &_isLoading {
            background: unset;
            box-shadow: unset;
        }
    }

    &__ResultImage {
        width: 100%;
        height: 350px;
        object-fit: cover;
        border-radius: 15px 15px 0 0;
    }

    &__ResultInfo{
        padding: 40px;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    &__ResultInfoRow {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
    }

    &__Suggestions {
        display: flex;
        flex-direction: column;
    }

    &__Suggestions-title {
        width: 100%;
        text-align: center;
        color: white;
        font-weight: bold;
        font-size: 20px;
        padding-bottom: 20px;
    }

    &__Suggestions-desc {
        padding-bottom: 20px;
        text-align: justify;
    }

    &__RelatedExplosions {
        text-align: center;
        color: var(--primary-color);
        font-size: 40px;
    }
}
