@import "./../../style/base/Card.scss";

.AboutCard {
    @extend %Card;

    &__Background {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        height: 100%;
        width: 100%;
        object-fit: cover;
        filter: brightness(0.6);
    }

    &__Info {
        z-index: 10;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    &__Title {
        font-size: 86px;
    }

    &__Description {
        font-size: 30px;
        text-align: center;
        padding-bottom: 10%;
    }

    &__Menu {
        width: 200px;
        height: 60px;
        border-radius: 100px !important;
        font-weight: 700 !important;
        font-size: 20px !important;

        svg {
            height: 35px;
            width: 35px;
        }
    }
}
