@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700&family=Roboto:wght@300;400;500;600;700&family=Roboto+Condensed:wght@700&family=Source+Sans+Pro&display=swap');

*,
*::after,
*::before {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
  line-height: 160%;
  text-decoration: none;
  position: relative;
  color: #fff;
  border: none;
  outline: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary-color: #ff4400;
  --primary-color-dark: #B85136;
  --container-size: 1440px;
  --primary-background: #171319;
  --primary-background-light: #3a3a3a;
}
